import { useState } from 'react';
import ChatForm from '../components/ChatForm';
import ChatHistoryPane from '../components/ChatHistoryPane'; // Ensure this path is correct

export default function Home() {
  const [messages, setMessages] = useState([]); // Holds all chat messages (current + history)
  const [isLoading, setIsLoading] = useState(false);
  const [showHistory, setShowHistory] = useState(false); // State to toggle history view

  const handleSendMessage = async (newMessage) => {
    setMessages((prevMessages) => [
      ...prevMessages,
      { text: newMessage, isUser: true, timestamp: new Date() },
    ]);
    setIsLoading(true);

    try {
      // Call to fetch response (API or similar)
      const response = await fetchChat(newMessage);
      setMessages((prevMessages) => [
        ...prevMessages,
        { text: response, isUser: false, timestamp: new Date() },
      ]);
    } catch (error) {
      console.error('Error fetching chat response:', error);
      setMessages((prevMessages) => [
        ...prevMessages,
        { text: 'Sorry, I encountered an error.', isUser: false, timestamp: new Date() },
      ]);
    } finally {
      setIsLoading(false);
    }
  };

  const toggleHistory = () => {
    setShowHistory((prevState) => !prevState); // Toggle history visibility
  };

  return (
    <div className="flex flex-col h-screen">
      <head>
        <title>Next.js Chatbot</title>
        <meta name="description" content="A simple chatbot UI built with Next.js" />
        <link rel="icon" href="/favicon.ico" />
      </head>

      {/* Show History Button */}
      <button
        onClick={toggleHistory}
        className="absolute top-5 left-5 z-10 bg-[#66fcf1] text-black p-2 rounded"
      >
        {showHistory ? 'Hide History' : 'Show History'}
      </button>

      {/* Main Content Area */}
      {showHistory ? (
        // Fullscreen Chat History
        <div className="flex-1 w-full">
          <ChatHistoryPane messages={messages} />
        </div>
      ) : (
        // Chat Interface
        <div className="flex-1 p-5">
          <h1 className="text-3xl mb-5">Chat with me!</h1>

          {/* Display current chat messages */}
          <div style={{ marginBottom: '20px' }}>
            {messages.map((message, index) => (
              <div key={index}>
                <div className={`message-container ${message.isUser ? 'user' : 'bot'}`}>
                  <div className={`label ${message.isUser ? 'right' : 'left'}`}>
                    {message.isUser ? 'ðŸ‘¤ You' : 'ðŸ¤– Bot'}
                  </div>
                  <div className={`message ${message.isUser ? 'user' : 'bot'}`}>
                    {message.text}
                  </div>
                </div>
              </div>
            ))}
            {isLoading && <div className="message-container bot">Thinking...</div>}
          </div>

          {/* Chat Form to send message */}
          <ChatForm onSendMessage={handleSendMessage} disabled={isLoading} />
        </div>
      )}

      <style jsx>{`
        .message-container {
          display: flex;
          flex-direction: column;
          margin-bottom: 10px;
        }

        .user {
          align-items: flex-end;
	  color = white;
        }

        .bot {
          align-items: flex-start;
        }

        .label {
          font-weight: bold;
        }

        .message {
          padding: 10px;
          background-color: #333;
          border-radius: 8px;
          margin-top: 5px;
          max-width: 70%;
        }

        /* Styling for the show history button */
        button {
          transition: background 0.3s;
        }

        button:hover {
          background-color: #52d6cd;
        }
      `}</style>
    </div>
  );
}

